.destination-back {
  background-image: url(../components/assets/destination/background-destination-desktop.jpg);
  padding-top: 5vh;
  background: no-repeat cover;
  height: 100vh;
}

/* font-family: 'Bellefair', serif; */
/* font-family: 'Barlow Condensed', sans-serif; */

:root {
  --white: #fbffff;
  --grey: #4a4e57;
  --light-grey: #bec0ca;
}
.destination-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: white;
}
.destination-left {
  margin-left: 25%;
}
.destination-left div {
  display: flex;
  gap: 1em;
  width: 80%;
  font-size: 1.2em;
  margin-bottom: 8vh;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 4px;
  color: var(--white);
}
.destination-left div p:first-child {
  color: var(--grey);
  font-weight: 800;
}
.destination-left div p {
  display: inline;
}
.destination-left img {
  height: 65%;
  margin-left: 10%;
}

.destination-right {
  display: flex;
  flex-direction: column;
  gap: 2em;
  width: 70%;
  margin-top: 7vh;
  color: var(--light-grey);
}
.planet {
  display: flex;
  justify-content: space-between;
  font-family: "Barlow Condensed", sans-serif;
  width: 70%;
  letter-spacing: 2px;
  margin-bottom: 3vh;
  overflow:visible;
}
.planet p {
  position: relative;
  z-index: 1;
  overflow:visible;
  
}
.planet p::after {
  overflow:visible;
  position: absolute;
  content: "";
  height: 100%;
  border-bottom: 2.5px solid var(--white);
  bottom: -2vh;
  left: 0;
  width: 0;
}
.planet p:hover::after {
  width: 100%;
}
.planet p:hover:after{
  width: 100%;
}
.active::after {
  width: 100% !important;
}
.destination-right > p:nth-child(2) {
  font-size: 5em;
  font-family: "Bellefair", serif;
  color: var(--white);
}
.info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1em;
  font-family: "Barlow Condensed", sans-serif;
  /* place-items: center; */
  color: var(--light-grey);
  font-size: 0.9em;
  letter-spacing: 1.2px;
}
.info p:nth-child(3),
.info p:last-child {
  font-family: "Bellefair", serif;
  font-size: 1.7em;
  color: var(--white);
}
.destination-right hr {
  height: 0.4px;
  opacity: 0.3;
}
@media screen and (max-width: 480px) {
  .destination-back {
    padding: 0;
    background-image: url(../components//assets/destination/background-destination-mobile.jpg);

    height: auto;
  }
  .destination-main {
    display: flex;
    flex-direction: column;
  }
  .destination-left {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 25vh;

    margin-bottom: 4vh;
  }
  .destination-left div {
    margin-bottom: 5vh;
    width: 100%;
    justify-content: center;
    gap: 0.7em;
    font-size: 0.9em;
  }
  .destination-left img {
    height: 100%;
    margin: 0;
  }
  .destination-right {
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 0 4vw;
  }
  .planet {
    width: 80%;
  }
  .destination-right > p:nth-child(2) {
    font-size: 3.5em;
  }
  .destination-right > p:nth-child(3) {
    font-size: 0.8em;
    text-align: center;
  }
  .destination-right hr {
    width: 90%;
  }
  .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2vh;
  }
  .info p:nth-child(1) {
    order: 1;
  }
  .info p:nth-child(2) {
    order: 3;
  }
  .info p:nth-child(3) {
    font-size: 2em;
    order: 2;
  }
  .info p:nth-child(4) {
    order: 4;
    font-size: 2em;
  }
}
