/* font-family: 'Bellefair', serif; */
/* font-family: 'Barlow Condensed', sans-serif; */
.crew-back {
  background-image: url(../components/assets/crew/background-crew-desktop.jpg);
  padding-top: 5vh;
  background: no-repeat cover;
  height: 100vh;
}
:root {
  --l-white: white;
  --white: #fbffff;
  --grey: #4a4e57;
  --light-grey: #bec0ca;
}

.crew-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: white;
  height: 80vh;
}
.crew-left {
  margin-left: 25%;
  overflow: hidden;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 0.8em;
}

.crew-left > div:first-child {
  display: flex;
  gap: 0.8em;
  width: 50%;
  font-size: 1.2em;
  margin-bottom: 12vh;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 4px;
  color: var(--white);
}
.crew-left b {
  color: var(--grey);
  font-weight: 800;
}
.crew-left > p:nth-child(2) {
  color: var(--light-grey);
  font-size: 1.3em;
  font-family: "Bellefair", serif;
  font-weight: 500;
}
.crew-left > p:nth-child(3) {
  color: var(--l-white);
  font-size: 3em;
  font-family: "Bellefair", serif;
}
.crew-left > p:nth-child(4) {
  color: var(--light-grey);
  width: 75%;
  font-size: 0.9em;
}
.dots {
  display: flex;
  margin-top: 6vh;
  gap: 1em;
  height: 7vh;
}
.dots * {
  background-color: var(--grey);
  content: "";
  height: 1.8vh;
  aspect-ratio: 1/1;
  border-radius: 50%;
}
.dots *:hover {
  background-color: var(--l-white);
}
.crew-active {
  background-color: var(--l-white);
}
.crew-right {
  margin-top: 5vh;
  margin-left: 5vw;
  overflow: hidden;
  height: auto;
}
.crew-right img {
  height: 70%;
  position: absolute;
  bottom: 0;
}
@media screen and (max-width: 480px) {
  .crew-back {
    background-image: url(../components/assets/crew/background-crew-mobile.jpg);
    padding: 0;
    height: auto;
  }
  .crew-main {
    display: flex;
    flex-direction: column;
  }
  .crew-left {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin: 0;
    height: 100vh;
    width: 100%;
  }
  .crew-left > div:first-child {
    width: 100%;
    margin-bottom: 40vh;
    justify-content: center;
  }
  .crew-left > p:nth-child(2) {
    font-size: 1em;
  }
  .crew-left > p:nth-child(3) {
    font-size: 2em;
  }
  .crew-left > p:nth-child(4) {
    width: 85%;
    font-size: 0.8em;
    text-align: center;
  }
  .crew-right {
    position: static;
  }
  .crew-right img {
    position: absolute;
    height: 30%;
    top: 20vh;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .dots {
    position: absolute;
    top: 45vh;
  }
}
