




.main-back{
  background-image: url(../components/assets/home/background-home-desktop.jpg) ;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  padding-top: 5vh;
}

.main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80%;
  height: auto;
  margin: 4vh auto;
  margin-top: 20vh;
}
.left {
  width: 70%;
  height: max-content;
  margin-left: auto;
  color: var(--white);
  font-family: "Barlow Condensed", sans-serif;
}
.left p:first-child {
  font-size: 1.8em;
  margin-right: auto;
  width: 90%;
}
.left p:nth-child(3) {
  font-size: 7em;
  font-family: "Bellefair", serif;
  color: white;
}
.left p:last-child {
  line-height: 1.5em;
  width: 95%;
  letter-spacing: 0.8px;
}
.right {
  height: 100%;
  position: relative;
}
.explore {
  font-family: "Bellefair", serif;
  display: grid;
  position: absolute;
  place-items: center;
  height: 70%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: white;
  font-size: 1.5em;
  letter-spacing: 2px;
  bottom: 0;
  right: 0;
}
@media screen and (max-width: 480px) {
  .main-back{
    padding-top: 0;
    background-image: url(../components//assets/home/background-home-mobile.jpg);
  }
  .main{
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr;
    margin-top: 3vh;
    
    width: 95%;
  }
  
  .left{
    margin-left: 0;
    width: 100%;
    text-align: center;
  }
  .left p:first-child {
    font-size: 1.2em;
  }
  .left p:nth-child(3) {
    font-size: 4em;
  }

  .left p{
    width: 100% !important;
    padding: 0 3vw !important;
  }
  .right{
    position: static;
    display: grid;
    place-items: center;
    margin-top: 5vh;
  }
  .explore{
    position: static;
  }
}


