nav {
  display: flex;
  width: 100%;
  height: 12vh;
  align-items: center;
  font-family: "Barlow Condensed", sans-serif;
}
nav img:first-child {
  background-color: white;
  border-radius: 50%;
  margin-left: 2vw;
}
nav img:nth-child(2){
  display: none;
}
nav .line {
  position: relative;
  height: 1px;
  width: 45%;
}
nav .line::after {
  position: absolute;
  content: "";
  background-color: rgb(131, 131, 131);
  height: 100%;
  width: 100%;
  left: 5vw;
  z-index: 1;
}
nav .ul {
  margin-left: auto;
  width: 60%;
  height: 100%;
  backdrop-filter: blur(100px);
}
nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  height: 100%;
  margin: auto;
}
nav li {
  display: flex;
  position: relative;
  list-style-type: none;
  color: var(--white);
  letter-spacing: 2px;
  font-size: 0.9em;
  height: 100%;
  align-items: center;
  gap: 0.5em;
}
li a {
  text-decoration: none;
  color: var(--white);
  z-index: 1;
}
nav li::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 100%;
  bottom: 0;
  border-bottom: 2px solid white;
}
nav li:hover::after {
  width: 100%;
}
nav b {
  color: white;
}

.nav-active::after {
  width: 100%;
}
@media screen and (max-width: 480px) {
  nav{
    padding:  0  8vw;
  }
  nav  .line{
   display: none;
  }
  nav .ul{
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    height: 100vh;
    width: 70%;
   z-index: 2;
  }
  nav ul{
    flex-direction: column;
    margin-top: 30%;
    margin-left: 15%;
    height: 40%;
    align-items: flex-start;
  }
  
  nav li::after{
    
    border: 0;
    top: auto;
    bottom: auto;
    left: -10px;
    height: 50%;
    width: 0;
  }
  nav img:first-child{
    height: 5vh;
  }
  nav img:nth-child(2){
    height: 3.5vh;
    display: block;
    margin-left: auto;
    z-index: 3;
  }
  
  .nav-active::after{
    background-color: white;
    height: 50%;
    width: 2px;
  }
  nav li:hover::after {
    width: 2px;

  }
}