/* font-family: 'Bellefair', serif; */
/* font-family: 'Barlow Condensed', sans-serif; */
.tech-back {
  background-image: url(../components/assets/technology/background-technology-desktop.jpg);
  padding-top: 5vh;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}
:root {
  --l-white: white;
  --white: #fbffff;
  --grey: #4a4e57;
  --light-grey: #bec0ca;
}

.tech-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: white;
  height: 80vh;
}
.tech-left {
  margin-left: 25%;
  margin-top: 5vh;
  overflow: hidden;
  height: 90%;
  display: flex;
  flex-direction: column;
  gap: 0.8em;
}

.tech-left > div:first-child {
  display: flex;
  gap: 0.8em;
  width: 50%;
  font-size: 1.2em;
  margin-bottom: 10vh;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 4px;
  color: var(--grey);
}
.tech-left > div > p:last-child {
  color: var(--l-white);
}
.tech {
  display: flex;
  gap: 2em;
}

.tech-dots {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.tech-dots div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 9vh;
  aspect-ratio: 1/1;
  border: 1px solid var(--grey);
  color: var(--light-grey);
  border-radius: 50%;
  text-align: center;
  font-size: 1.5em;
  font-family: "Bellefair", serif;
}
.tech-active {
  border: 0px;
  color: var(--grey);
  background-color: var(--l-white);
}
.tech-info {
  display: flex;
  flex-direction: column;
  gap: 0.6em;
  height: 100%;
  width: 80%;
  color: var(--light-grey);
}
.tech-info p:first-child {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 0.8em;
  letter-spacing: 2px;
}
.tech-info p:nth-child(2) {
  font-size: 3em;
  font-family: "Bellefair", serif;
  color: var(--l-white);
}
.tech-info p:last-child {
  width: 90%;
  font-size: 0.8em;
  line-height: 22px;
  text-align: left;
}
.tech-right {
  display: flex;
  height: 90%;
}
.tech-right img {
  margin: auto 0;
  margin-left: auto;
  height: 80%;
}
@media screen and (max-width: 480px) {
  .tech-back {
    background-image: url(../components/assets/technology/background-technology-mobile.jpg);

    padding-top: 0;
    overflow: hidden;
    height: auto;
  }

  .tech-main {
    display: flex;
    flex-direction: column;
    height: 90vh;
  }
  .tech-left {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    height: auto;
    width: 100%;
  }
  .tech-left > div:first-child {
    width: 100%;
    margin-bottom: 30vh;
    justify-content: center;
  }
  .tech-info p:nth-child(2) {
    font-size: 2em;
  }
  .tech {
    flex-direction: column;
    gap: 1em;
  }
  .tech-dots {
    margin: 0 auto;
    flex-direction: row;
    width: 50%;
    height: 10vh;
  }
  .tech-dots div {
    font-size: 1em;
    height: 6vh;
  }
  .tech-info {
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .tech-info * {
    width: 90%;
    text-align: center;
  }
  .tech-info p:last-child {
    text-align: center;
  }
  .tech-right {
    height: 0;
    width: 0;
  }
  .tech-right img {
    position: absolute;
    top: 20vh;
    height: 25vh;
    width: 100%;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}
